import React from 'react'
import Link from 'gatsby-link'

const IndexPage = () => (
  <div>
    <article>
      <h1>Hi people</h1>
      <p>Nice to meet you.</p>
      <Link to="/about/" className="btn btn-primary">Learn More</Link>
    </article>
  </div>
)

export default IndexPage



